@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@400;500;600;700&display=swap);
.App {
	min-height: 100vh;
	display: grid;
	grid-template-areas: "header header" "main bag";
	grid-gap: 24px 50px;
	gap: 24px 50px;
	background-color: #1D2226;
	font-family: 'Manrope', sans-serif;
  }
  
  .App > header {
	grid-area: header;
  }
  
  .App > main {
	grid-area: main;
  }
  
  .App > .bag {
	grid-area: bag;
	margin-right: 96px;
  }
  
  .content {
	padding-left: 96px;
	padding-bottom: 24px;
	position: relative;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
  }
  
  .top-movies {
	margin-bottom: 40px;
  }
  
  .top-movies h2, .movies h2 {
	color: #FFF;
	margin-bottom: 16px;
  }
  
  .movies-grid {
	max-width: 937px;
	display: grid;
	grid-template-columns: repeat(auto-fit, 168px);
	grid-gap: 24px;
	gap: 24px;
  }
  
  .movies .search-container {
	display: none;
  }

  * {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
  }
  
  body {
	font-family: 'Manrope', sans-serif;
	overflow-x: hidden;
  }
  
  html {
	background: #1D2226;
  }
.search-container {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    position: relative;
    display: -webkit-flex;
    display: flex;
    margin-right: 92px;
  }
  
  .search-container__input {
    width: 100%;
    padding: 16px 14px;
    border: 0;
    outline: 0;
    background: #171B1E;
    color: #FFF;
    border: 1px solid transparent;
  }
  
  .search-container__input:hover {
    border: 1px solid #8C8C8C;
  }
  
  .search-container__input:focus {
    border: 1px solid #FDFDFD;
  }
  
  .search-container__input::-webkit-input-placeholder {
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    color: #FDFDFD;
    opacity: 0.7;
  }
  
  .search-container__input:-ms-input-placeholder {
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    color: #FDFDFD;
    opacity: 0.7;
  }
  
  .search-container__input::placeholder {
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    color: #FDFDFD;
    opacity: 0.7;
  }
  
  .search-container__icon {
    width: 24px;
    height: 24px;
    cursor: pointer;
    -webkit-align-self: center;
            align-self: center;
    position: absolute;
    z-index: 1;
    right: 15px;
  }
.header {
    background: #0F1C26;
    color: #FDFDFD;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding: 24px 100px 24px 70px;
  }
  
  .header__logo {
    margin-right: 42px;
  }
  
  .header__item-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 8px;
    gap: 8px;
  }
  
  .item-container__img {
    object-fit: contain;
    width: 56px;
    height: 56px;
  }
  
  .mr-95 {
    margin-right: 95px;
  }
.movie-card {
    height: 300px;
    padding: 12px 8px 6px 8px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.4);
  }
  
  .movie-card > svg {
    cursor: pointer;
  }
  
  
  .movie-info {
    margin-top: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .movie-info h3 {
    color: #FFF;
    max-width: 100px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .movie-info .movie-stars {
    color: #FBCD6E;
  }
.price-button {
    all: unset;
    box-sizing: border-box;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-weight: bold;
  }
  
  .price-button.movie {
    padding: 5px 8px;
    font-size: 12px;
    background: #A785ED;
    color: #EEE5FF;
  }
  
  .price-button.basket {
    padding: 16px;
    font-size: 16px;
    background: #E25EBF;
    color: #FDFDFD;
  }
  
  .price-button.movie:hover {
    background: linear-gradient(0deg, rgba(46, 46, 46, 0.45), rgba(46, 46, 46, 0.45)), #A785ED;
  }
  
  .price-button.movie:active {
    background: linear-gradient(0deg, rgba(123, 123, 123, 0.45), rgba(123, 123, 123, 0.45)), #A785ED;
  }
  
  .price-button.basket:hover {
    background: linear-gradient(0deg, rgba(225, 123, 123, 0.45), rgba(225, 123, 123, 0.45)), #FFB6EB;
    box-shadow: 0px 16px 24px -12px rgba(226, 94, 191, 0.16);
  }
  
  .price-button.basket:active {
    background: linear-gradient(0deg, rgba(225, 123, 123, 0.6), rgba(225, 123, 123, 0.6)), #FFB6EB;
  box-shadow: 0px 16px 24px -12px rgba(226, 94, 191, 0.5);
  }
  
.bag {
    width: 312px;
    height: 560px;
    color: #fff;
    position: -webkit-sticky;
    position: sticky;
    top: 127px;
    overflow-y: auto;
    background-color: #1d2226;
}

.bag .bag__title {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: linear-gradient(0deg, #a785ed, #a785ed), #c4c4c4;
    -webkit-align-self: center;
            align-self: center;
}

.bag .bag__container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background: rgba(0, 0, 0, 0.2);
    grid-gap: 24px;
    gap: 24px;
    padding: 40px 16px;
}

.bag__container h3 {
    font-size: 16px;
    -webkit-align-self: center;
            align-self: center;
}

.bag__container h2,
.bag__container .bag__illustration {
    -webkit-align-self: center;
            align-self: center;
}

.coupon-container span {
    font-size: 12px;
    margin-bottom: 3px;
}

.coupon-container__input-group {
    width: 100%;
    height: 40px;
    position: relative;
    display: -webkit-flex;
    display: flex;
}

.coupon-container__input {
    width: 100%;
    padding: 16px 14px;
    border: 0;
    outline: 0;
    background: #0b1217;
    color: #fff;
    border: 1px solid transparent;
}

.coupon-container__input:hover {
    border: 1px solid #8c8c8c;
}

.coupon-container__input:focus {
    border: 1px solid #fdfdfd;
}

.coupon-container__input::-webkit-input-placeholder {
    font-family: "Manrope", sans-serif;
    font-weight: 500;
    color: #fdfdfd;
    opacity: 0.7;
}

.coupon-container__input:-ms-input-placeholder {
    font-family: "Manrope", sans-serif;
    font-weight: 500;
    color: #fdfdfd;
    opacity: 0.7;
}

.coupon-container__input::placeholder {
    font-family: "Manrope", sans-serif;
    font-weight: 500;
    color: #fdfdfd;
    opacity: 0.7;
}

.coupon-container__icon {
    width: 24px;
    height: 24px;
    -webkit-align-self: center;
            align-self: center;
    position: absolute;
    z-index: 1;
    right: 15px;
}

.bag__movie {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.bag__movie img {
    width: 45px;
    height: 65px;
    margin-right: 13px;
}

.bag__movie__info {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
}

.bag__movie__actions {
    display: -webkit-flex;
    display: flex;
    grid-gap: 14px;
    gap: 14px;
}

.bag__movie__actions svg:hover {
    background: linear-gradient(0deg, rgba(107, 107, 107, 0.37), rgba(107, 107, 107, 0.37));
}

.bag__movie__actions svg:active {
    background: linear-gradient(0deg, rgba(191, 191, 191, 0.37), rgba(191, 191, 191, 0.37));
}

.bag__movie__actions svg {
    cursor: pointer;
}
.top-movies {
    color: #FDFDFD;
    margin-bottom: 40px;
}

.top-movies h2 {
    margin-bottom: 16px
}
